"use client"; // Assuming client-side interaction for fetching/running scenarios

import React, { useState, useEffect } from 'react';

// Define a type for scenario data from API
type Scenario = {
  name: string;
  description: string;
  filename: string; // Added filename from API
};

// API Server Base URL (adjust if needed, e.g., use environment variable)
const INJECTOR_API_BASE_URL = 'http://localhost:8080';

export default function Home() {
  const [scenarios, setScenarios] = useState<Scenario[]>([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);
  const [runningScenario, setRunningScenario] = useState<string | null>(null);
  const [result, setResult] = useState<string | null>(null);

  // Fetch scenarios from Go backend
  useEffect(() => {
    const fetchScenarios = async () => {
      setLoading(true);
      setError(null);
      try {
        const response = await fetch(`${INJECTOR_API_BASE_URL}/api/scenarios`);
        if (!response.ok) {
          throw new Error(`Failed to fetch scenarios: ${response.statusText}`);
        }
        const data: Scenario[] = await response.json();
        setScenarios(data || []);
      } catch (err: any) {
        setError(`Failed to load scenarios: ${err.message}`);
        console.error(err);
      }
      setLoading(false);
    };

    fetchScenarios();
  }, []);

  const handleRunScenario = async (scenarioFilename: string) => {
    setRunningScenario(scenarioFilename); // Use filename as identifier
    setResult(null);
    setError(null);
    console.log(`Running scenario: ${scenarioFilename}`);
    try {
      const response = await fetch(`${INJECTOR_API_BASE_URL}/api/run-scenario`, {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({ filename: scenarioFilename }),
      });

      const responseData = await response.json(); // Read body regardless of status

      if (!response.ok) {
        throw new Error(responseData.message || `Scenario execution failed: ${response.statusText}`);
      }

      setResult(`Scenario ${scenarioFilename} executed successfully. Result: ${responseData.message}`);
    } catch (err: any) {
      setError(`Failed to run scenario ${scenarioFilename}: ${err.message}`);
      console.error(err);
    }
    setRunningScenario(null);
  };

  return (
    <main className="flex min-h-screen flex-col items-center p-12 bg-gradient-to-b from-gray-900 to-gray-800 text-gray-100">
      <h1 className="text-4xl font-bold mb-8">Day32 Packet Injector</h1>

      <div className="w-full max-w-4xl bg-gray-800 shadow-xl rounded-lg p-6">
        <h2 className="text-2xl font-semibold mb-4 text-teal-400">Available Scenarios</h2>
        {loading && <p className="text-gray-400">Loading scenarios...</p>}
        {error && <p className="text-red-500 bg-gray-900 p-3 rounded">Error: {error}</p>}
        {!loading && !error && scenarios.length === 0 && (
             <p className="text-gray-400 italic">No scenarios found. Make sure the Go API server is running and can access the 'scenarios' directory.</p>
        )}
        {!loading && !error && scenarios.length > 0 && (
          <ul className="space-y-3">
            {scenarios.map((scenario) => (
              <li key={scenario.filename} className="p-4 bg-gray-700 rounded-md flex justify-between items-center shadow">
                <div>
                  <p className="font-medium text-lg text-gray-50">{scenario.name} <span className="text-xs text-gray-500">({scenario.filename})</span></p>
                  <p className="text-sm text-gray-400">{scenario.description}</p>
                </div>
                <button
                  onClick={() => handleRunScenario(scenario.filename)} // Pass filename
                  disabled={!!runningScenario}
                  className={`px-4 py-2 rounded ${runningScenario ? 'bg-gray-600 cursor-not-allowed' : 'bg-teal-600 hover:bg-teal-700'} text-white transition duration-150 ease-in-out disabled:opacity-50`}
                >
                  {runningScenario === scenario.filename ? 'Running...' : 'Run'}
                </button>
              </li>
            ))}
          </ul>
        )}

        {/* Display result or error from running scenario */}
        {result && (
          <div className="mt-6 p-4 bg-green-900 border border-green-700 rounded-md text-green-200">
            <h3 className="font-semibold mb-2">Execution Result:</h3>
            <pre className="text-sm whitespace-pre-wrap">{result}</pre>
          </div>
        )}
        {/* Error from handleRunScenario is displayed near the top list now */}
      </div>

      <div className="w-full max-w-4xl mt-8 bg-gray-800 shadow-xl rounded-lg p-6">
          <h2 className="text-2xl font-semibold mb-4 text-orange-400">Target Stack Logs</h2>
          <p className="text-gray-400 italic">
              Please monitor the logs of the running Day32 network stack (Go process)
              in its terminal window to observe the detailed effects of the injected packets.
          </p>
          {/* Potential future enhancement: Display logs via WebSocket? */}
      </div>

    </main>
  );
}
